import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class ShippersService {
  constructor(private http: HttpClient) {}

  shippers = new BehaviorSubject(null);

  getAll() {
    return this.http.get(baseUrl + '/shipper').subscribe((res) => {
      this.shippers.next(res['data']);
    });
  }

  addShipper(shipperId) {
    return this.http.post(baseUrl + `/shipper/addShipper/${shipperId}`, {});
  }

  deleteShipper(shipperId) {
    return this.http.delete(baseUrl + `/shipper/deleteShipper/${shipperId}`);
  }

  lockOne(itemId) {
    return this.http.get(baseUrl + `/shipper/lock/${itemId}`);
  }

  unlockOne(itemId) {
    return this.http.get(baseUrl + `/shipper/unlock/${itemId}`);
  }
}
